.section {
  margin: 0 auto;
  padding: 100px 0;
  color: #fff;
  background: #5957cd;
  display: flex;
  flex-direction: column;
  //justify-content: center
  align-items: center; }

.subtitle {
  text-align: center;
  margin: 18px 0 16px;
  font-size: 16px;
  font-weight: 400;
  max-width: 750px; }

.actions {
  margin: 26px 0 0;
  position: relative;
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  &:before {
    border-bottom: 1px dashed hsla(0, 0%, 100%, .6);
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    z-index: 0; } }

.action {
  position: relative;
  //width: 300px
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  &:first-child {
    margin: 0 0 0 50px; }
  &:last-child {
    margin: 0 50px 0 0; }
  &_pos_top {
    align-items: flex-start; } }

.circle {
  position: absolute;
  top: calc( 50% - 20px );
  left: calc( 50% - 20px );
  z-index: 1;
  width: 40px;
  height: 40px;
  border: 3px solid #fff;
  border-radius: 50%;
  background: #4241b8;
  &:before {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    content: "";
    position: absolute;
    top: calc( 50% - 12px );
    left: calc( 50% - 12px );
    background: #ff67cb; } }

.actionText {
  font-size: 16px;
  text-align: center; }

.verticalLine {
  position: absolute;
  bottom: 55px;
  left: 50%;
  width: 1px;
  height: 70px;
  border-left: 1px dashed hsla(0, 0%, 100%, .6);
  &:after {
    content: '';
    width: 5px;
    height: 5px;
    position: absolute;
    bottom: -7px;
    left: -3px;
    background: #c9c8c8;
    border-radius: 50%; }
  &_pos_top {
    transform: rotate(180deg);
    top: 55px; } }

.svgIcon {
  position: absolute;
  left: calc( 50% - 12px );
  top: -32px;
  &_pos_top {
    top: 40px; } }

