.footer {
  padding: 24px 0;
  background: #4b49c5;
  color: #fff; }

.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: 0 0 34px; }

.copyrightContainer {
  display: flex;
  justify-content: center;
  align-items: center; }

.copyright {
  font-size: 14px;
  margin: 0 10px 0 0; }
.policy {
  color: #ffffff;
  text-decoration: none;
  font-size: 14px; }

.column {
  display: flex;
  flex-direction: column; }

.columnLink {
  text-decoration: none;
  color: #fff;
  margin: 0 0 18px; }

.columnTitle {
  margin: 0 0 16px; }


.list {
  display: flex;
  flex-direction: column;
  li {
    margin: 0 0 8px; } }

.navLink {
  color: #ffffff;
  text-decoration: none; }

.roboweb {
  padding: 30px 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  a {
    color: #fb6e4d;
    text-decoration: none; } }
