.button {
  background: transparent;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
  outline: none;
  overflow: hidden;
  padding: 10px 40px;
  position: relative;
  border: 2px solid #ff9d02;
  color: #ff9d02;
  transition: all .3s linear;
  &:after {
    background: #ff9d02;
    bottom: 0;
    content: "";
    height: 100%;
    left: -10%;
    position: absolute;
    transform: skewx(-45deg);
    transition: .5s;
    width: 17%;
    z-index: -1; }
  &:hover {
    color: #fff;
    &:after {
      left: -10%;
      width: 120%; } }
  @media screen and (max-width: 768px) {
    margin: 20px 0 10px; }
  @media screen and (max-width: 500px) {
    margin: 12px 0 6px; } }
